<template>
  <div class="flex flex-col w-full overflow-scroll p-6">
    <h1 class="text-2xl mb-6 dark:text-zinc-400">Update projects section</h1>

    <div class="flex flex-row gap-2 mb-6">
      <Button
        label="New"
        class="h-10"
        icon="pi pi-plus"
        severity="success"
        size="small"
        @click="newItem" />
      <Button
        label="Edit"
        class="h-10"
        icon="pi pi-pencil"
        severity="info"
        :disabled="isEditDisabled"
        size="small"
        @click="editItem" />
      <Button
        label="Delete"
        class="h-10"
        icon="pi pi-trash"
        severity="danger"
        :disabled="isDeleteDisabled"
        size="small"
        @click="deleteItems" />
    </div>

    <DataTable
      ref="dt"
      dataKey="id"
      :value="projects"
      v-model:selection="selected">
      <Column
        selectionMode="multiple"
        :exportable="false" />
      <Column
        field="title"
        header="Title">
      </Column>
      <Column
        field="description"
        header="Description">
      </Column>
      <Column
        field="owner"
        header="Owner" />
      <Column
        field="screenshots"
        header="Screenshots">
        <template #body="{ data }">{{ data.screenshots.length }}</template>
      </Column>
    </DataTable>
  </div>
</template>

<script setup lang="ts">
  import Button from "primevue/button"
  import Column from "primevue/column"
  import DataTable from "primevue/datatable"
  import { useProjectStore } from "./projects.store"
  import { onMounted } from "vue"
  import { storeToRefs } from "pinia"
  const store = useProjectStore()
  const { isEditDisabled, isDeleteDisabled, projects, selected } =
    storeToRefs(store)
  const { newItem, editItem, deleteItems } = store
  onMounted(store.init)
</script>

